export default {
    state:{
        content:{},
        apiKey:"84f705024349b8b3ea335b44be07eb99",
        apiHost:"v3.football.api-sports.io",
        timezone:""
    },
    actions:{
        fetchContent:function({commit,state}){
          commit('getTimeZone')
          fetch(`https://v3.football.api-sports.io/fixtures?live=all&timezone=${state.timezone}`, {
	"method": "GET",
	"headers": {
		"x-rapidapi-host": state.apiHost,
		"x-rapidapi-key": state.apiKey
	}
        })
        .then(response => response.json())
        .then(response => {
            console.log(response);
            commit('updateContent',response.response)
        })
        .catch(err => {
            console.log(err);
        });
        }
    },
    mutations:{
        updateContent(state,obj){
            state.content = obj
        },
        getTimeZone(state){
            state.timezone = Intl.DateTimeFormat().resolvedOptions().timeZone
            console.log(state.timezone)
        }
    },
    getters:{
        getContent(state){
            return state.content
        }
    }
}