<template>
<div id="app">
    <div 
    :id="index" 
    v-for="(match,index) in getContent" 
    :key="index" 
    @click="matchActive($event.currentTarget.id)" 
    class="matches"
    >
      <app-League :match="match"></app-League>
      <app-Score :match="match"></app-Score>
      <app-Match :match="match"></app-Match>
    </div>
  </div>
</template>

<script>
import {mapActions,mapGetters} from "vuex"

export default {
  name: 'App',
  computed:mapGetters(['getContent']),
  methods:mapActions(['fetchContent','matchActive']),
mounted(){
  this.fetchContent() 
  if (confirm('данный скрипт включает обновление данных каждую минуту ,однако у данного API есть огроничение на 100 запросов в день поэтому нажимайте ок только если хотите опробывать данный функционал(если вы нажмете нет то странница всёровно загрузится просто данные не будут обновляться в LIVE формате)')) {
      setInterval(()=>{
     this.fetchContent() 
      },60 * 1000) 
    }
  }  
}
   
</script>

