<template>
    <div class="match">
        <app-Teams id="home" :team="match.teams.home.name" :imgSrc="match.teams.home.logo"></app-Teams>
        <div id="events">
            <h3>Match Events</h3>
            <div v-for="(event,index) in match.events" :key="index" class="events">
            <img :src="event.team.logo" width="25px" height="auto" alt="">
            <div v-if="event.type == 'Goal'&& event.assist.name != null">
                <img src="../assets/ball.png" width="15px" height="15px" alt="">
                <p>
                {{event.time.elapsed + "' " + event.detail + " (" + event.player.name + ")" + " assist" +  "(" + event.assist.name + ")"}}
                </p>
            </div>
            <div v-else-if="event.type == 'subst'">
                <img src="../assets/subst.png" width="15px" height="15px" alt="">
                <p>
                {{event.time.elapsed + "' " + event.detail + " (" + event.player.name + ")" + "(" + event.assist.name + ")"}}
                </p>          
            </div>
            <div v-else>
                <img v-if="event.detail == 'Yellow Card'" src="../assets/Yellow_card.png" width="15px" height="auto" alt="">
                <img v-else-if="event.detail == 'Red Card'" src="../assets/Red_card.png" width="15px" height="auto" alt="">
                <img v-else src="../assets/ball.png" width="15px" height="15px" alt="">
                <p>
                {{event.time.elapsed + "' " + event.detail + " (" + event.player.name + ")"}}
                </p>
            </div>
            <br>
            </div>
        </div>
        <app-Teams id="away" :team="match.teams.away.name" :imgSrc="match.teams.away.logo"></app-Teams>
        <div id="delimiter">
            <hr>          
        </div>
    </div>
</template>

<script>
export default {
    props:['match']
}
</script>

