export default {
    state:{
        elem:''
    },
    mutations:{
        oneElem(state,value){
            state.elem = value
        }
    },
    actions:{
        matchActive(ctx,id){    
            if (!document.querySelector('.active')) {
                document.getElementById(`${id}`).classList.add('active')
            }
                document.querySelector('.active').classList.remove('active')   
                document.getElementById(`${id}`).classList.add('active')
        }
    }
}